.page-title {
  margin-top: 0;
  margin-bottom: 30px;
  font-size: 24px;
  font-weight: $font-weight-400;
}

footer {
  padding: 0 30px;
  padding-bottom: 15px;
  position: absolute;
  right: 0;

  @include max-screen($break-small) {
    position: relative;
    text-align: center;
  }
}

.copyright {
  margin-bottom: 0;
  font-size: 13px;
}

.btn-default {
  color: $grey-600;
  background-color: $col-white;
  border-color: $grey-300;
  padding: 6px 18px;
}

.btn-toolbar > .btn-group {
  @extend .m-l-5;
}

.Droppable {
  > ul {
    padding-left: 0;
    .dd-item {
      .dd-content {
        .action {
          button:not(:last-child) {
            margin-right: 5px;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 767px) {
  .hidden-xs {
    display: none;
  }
}

@media only screen and (max-width: $break-medium) {
  .hidden-sm {
    display: none;
  }
}

@media only screen and (max-width: $break-1024) {
  .hidden-md {
    display: none;
  }
}

.echart-doughnut {
  width: 150px;
  height: 130px !important;
  margin: 0 auto;
}

.modal.fade.show {
  display: block;
  padding-right: 17px;
}

.cke_1
  .cke
  .cke_reset
  .cke_chrome
  .cke_editor_editor1
  .cke_ltr
  .cke_browser_webkit {
  border: 1px solid #f4f7f6;
}
.cke .cke_inner .cke_top {
  border-bottom-width: 0;
  .cke_toolbox {
    padding: 0 0 5px 5px;
    margin: 0;
    .cke_toolbar {
      margin-top: 5px;
      margin-right: 5px;
      margin-left: 0;
      min-width: 0;
      color: $grey-300;
      background-color: $col-white;
      border-color: $grey-300;
      padding: 4px;
    }
  }
}

.cke .cke_inner .cke_bottom {
  border-top: 0px solid #d1d1d1 !important;
  background-color: #f5f5f5;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  height: 2px;
  padding-top: 1px;
  cursor: ns-resize;
  .cke_resizer .cke_voice_label {
    width: 20px;
    margin: 1px auto;
    border-top: 1px solid #a9a9a9;
  }
}

.cke .cke_inner.cke_contents {
  width: 100%;
  padding: 10px;
  margin-bottom: 0;
  font-family: Menlo, Monaco, monospace, sans-serif;
  font-size: 14px;
  color: #ccc;
  background-color: #222;
  border: 0;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  box-shadow: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  resize: none;
}

.profile-header {
  text-align: center;
}

.leaflet-container {
  height: 600px;
  width: 100%;
  z-index: 1;
}

.hide-border {
  &:before {
    display: none !important;
  }
}
.dd-empty {
  border: 1px dashed #bbb;
  min-height: 100px;
  background-color: #e5e5e5;
  background-image: -webkit-linear-gradient(
      45deg,
      #fff 25%,
      transparent 25%,
      transparent 75%,
      #fff 75%,
      #fff
    ),
    -webkit-linear-gradient(45deg, #fff 25%, transparent 25%, transparent 75%, #fff
          75%, #fff);
  background-image: -moz-linear-gradient(
      45deg,
      #fff 25%,
      transparent 25%,
      transparent 75%,
      #fff 75%,
      #fff
    ),
    -moz-linear-gradient(45deg, #fff 25%, transparent 25%, transparent 75%, #fff
          75%, #fff);
  background-image: linear-gradient(
      45deg,
      #fff 25%,
      transparent 25%,
      transparent 75%,
      #fff 75%,
      #fff
    ),
    linear-gradient(
      45deg,
      #fff 25%,
      transparent 25%,
      transparent 75%,
      #fff 75%,
      #fff
    );
  -ms-background-size: 60px 60px;
  background-size: 60px 60px;
  background-position: 0 0, 30px 30px;
}

#main-content {
  margin: 90px 0 !important;
  padding-right: 30px !important;
}
@media screen and (max-width: 1200px) {
  #main-content {
    padding: 0 30px !important;
  }
}

li {
  cursor: pointer;
}


